@import url(https://fonts.googleapis.com/css?family=Saira);
* {
  font-family: 'Saira', sans-serif;
}

body {
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-Detail {
  background-color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

a.no-underline {
  text-decoration: none;
}

input, textarea { 
  background-color:#FFFFFF;
  border:solid 1px #A9A9A9;
  font-size:15px;
  color:#000000;
  border-radius:5px;
  padding-top:5px;
  padding-bottom:5px;
  padding-left:6px;
  padding-right:6px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.boxy {
  color: rgba(255, 255, 255, 0);
  display: inline-block;
  cursor: pointer;
  text-decoration: none;
  padding: 0.6em 1.6em;
  margin: 0.6em;
  border-style: solid;
  border-width: 1px;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  -webkit-border-image: -webkit-linear-gradient(295deg, #fb83fa 0%, #31bcb8 100%) 1 round;
          border-image: linear-gradient(155deg, #fb83fa 0%, #31bcb8 100%) 1 round;
  background: -webkit-linear-gradient(295deg, #fb83fa 0%, #31bcb8 100%);
  background: linear-gradient(155deg, #fb83fa 0%, #31bcb8 100%);
  background-clip: text;
  -webkit-background-clip: text; }
  .boxy:hover {
    box-shadow: 0 0 0px 1px rgba(255, 255, 255, 0.5), 2px 2px 6px rgba(49, 188, 184, 0.4), -2px -2px 6px rgba(251, 131, 250, 0.4); }
  .boxy:hover {
    color: rgba(255, 255, 255, 0.1);
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.4); }
  .boxy.red {
    -webkit-border-image: -webkit-linear-gradient(295deg, #fc5c3c 0%, #ffb638 100%) 1 round;
            border-image: linear-gradient(155deg, #fc5c3c 0%, #ffb638 100%) 1 round;
    background: -webkit-linear-gradient(295deg, #fc5c3c 0%, #ffb638 100%);
    background: linear-gradient(155deg, #fc5c3c 0%, #ffb638 100%);
    background-clip: text;
    -webkit-background-clip: text; }
    .boxy.red:hover {
      box-shadow: 0 0 0px 1px rgba(255, 255, 255, 0.5), 2px 2px 6px rgba(255, 182, 56, 0.4), -2px -2px 6px rgba(252, 92, 60, 0.4); }
  .boxy.green {
    -webkit-border-image: -webkit-linear-gradient(295deg, #06e5de 0%, #AAFFA9 100%) 1 round;
            border-image: linear-gradient(155deg, #06e5de 0%, #AAFFA9 100%) 1 round;
    background: -webkit-linear-gradient(295deg, #06e5de 0%, #AAFFA9 100%);
    background: linear-gradient(155deg, #06e5de 0%, #AAFFA9 100%);
    background-clip: text;
    -webkit-background-clip: text; }
    .boxy.green:hover {
      box-shadow: 0 0 0px 1px rgba(255, 255, 255, 0.5), 2px 2px 6px rgba(170, 255, 169, 0.4), -2px -2px 6px rgba(6, 229, 222, 0.4); }
  .boxy.la {
    text-transform: uppercase;
    font-weight: 300;
    letter-spacing: 0.05em;
    font-size: 3em;
    padding: 0.3em;
    border-width: 2px;
    display: block;
    width: 70%;
    margin: 0.6em auto;
    -webkit-border-image: -webkit-linear-gradient(295deg, #F0C27B 0%, #ad1ba3 100%) 1 round;
            border-image: linear-gradient(155deg, #F0C27B 0%, #ad1ba3 100%) 1 round;
    background: -webkit-linear-gradient(295deg, #F0C27B 0%, #ad1ba3 100%);
    background: linear-gradient(155deg, #F0C27B 0%, #ad1ba3 100%);
    background-clip: text;
    -webkit-background-clip: text; }
    .boxy.la:hover {
      box-shadow: 0 0 0px 1px rgba(255, 255, 255, 0.5), 2px 2px 6px rgba(173, 27, 163, 0.4), -2px -2px 6px rgba(240, 194, 123, 0.4); }

